body {
    font-family: @Font_Arimo;
    background-image: url("../assets/image-1.png");
    background-color: #888f8f;
    color: @color_primary_text;
    overflow: hidden;
    height: 100vh;
    position: relative;

    // @media @mobile {
    //     height: 100%;
    //     overflow-y: auto;
    // }

    &::-webkit-scrollbar {
        width: 0 !important;
    }

    video,
    audio {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 0;
        display: none;
    }

    #transition-container {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;

        #slider {
            width: 100%;
            height: 100%;
            transition: all 2s ease-in;
        }

        .blur {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background-color: #00000070;
        }
    }
}

header {
    margin-bottom: 80px;
    padding: 20px;
    position: relative;

    @media @mobile {
        margin-bottom: 30px;
    }

    #canvas-container {
        position: absolute;
        top: 20;
        right: 20;
        bottom: 20;
        left: 20;
        transition: all 2s ease-in;
    }

    img.logo {
        min-width: 100%;
        animation: open-mask 2s;
        animation-fill-mode: forwards;
    }

    img.logo.hidden {
        opacity: 0;
    }

    img.logo-alpha,
    img.displacement {
        display: none;
    }

    .logo-wrapper {
        width: 100%;
        overflow: hidden;
    }

    span.loading {
        position: absolute;
        .typography__text-small();
        color: @color_primary_description;
        bottom: 0;
        right: 0;

        &.done {
            -webkit-animation: scale-out-center 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) 0.5s both;
            animation: scale-out-center 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) 0.5s both;
        }
    }
}

footer {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    position: fixed;
    width: 100%;
    bottom: 0;
    gap: 140px;
    .typography__text-small();

    // @media @mobile {
    //     padding: 30px 20px 40px 20px;
    //     position: inherit;
    //     z-index: 10;
    // }

    .left-side {
        flex: 2;
        display: flex;
        align-items: center;
    }

    .right-side {
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media @tablet {
            justify-content: flex-end;
        }
    }

    a.created {
        color: @color_primary_description;

        span {
            .typography__text-bold--small();
        }
    }

    .footer-connect {
        display: flex;
        justify-content: space-between;
        align-items: center;

        a {
            margin-right: 20px;
        }

        @media @tablet {
            display: none;
        }

        svg {
            transition: all 0.3s linear;

            &:hover path {
                fill-opacity: 1;
            }
        }
    }
}

section.main-top {
    padding: 0 20px;
    display: flex;
    gap: 140px;
    overflow: hidden;

    @media @laptop {
        gap: 100px;
    }

    @media @mobile {
        gap: 40px;
    }

    .main-description {
        &_about {
            flex: 2;
            &-container::before {
                content: url("../assets/point-icon.svg");
                position: relative;
                right: 10px;

                @media @tablet {
                    content: "";
                }
            }
        }
    }

    .main-connect {
        &_about {
            flex: 1;
            @media @tablet {
                justify-content: flex-end !important;
            }

            @media @mobile {
                display: none !important;
            }

            &-actions {
                &.mobile-actions {
                    display: none;
                    position: absolute;
                    right: 0;

                    @media @mobile {
                        display: block;
                    }
                }
            }
        }
    }

    .main-description,
    .main-connect {
        &_about {
            display: flex;
            justify-content: space-between;
            -webkit-animation: slide-in-top 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1.4s both;
            animation: slide-in-top 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1.4s both;

            @media @mobile {
                flex-direction: column;
                position: relative;
            }

            &-title {
                .typography__text-bold();
            }

            &-position {
                .typography__text-regular();
                margin-left: 10px;

                @media @tablet {
                    display: none;
                }
            }

            p {
                .typography__text-regular();

                @media @tablet {
                    display: none;
                }
            }

            &-actions {
                margin-left: 20px;
                .typography__text-regular();

                &__container {
                    margin-top: 20px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }

                svg {
                    transition: all 0.3s linear;

                    &:hover path {
                        fill: @color_active;
                    }
                }
            }

            &-container {
                display: flex;
                align-items: center;
            }
        }
        &_links {
            margin-top: 120px;

            @media @mobile {
                margin-top: 100px;
            }
            ul {
                width: 100%;

                li {
                    position: relative;
                    cursor: pointer;
                    width: 100%;
                    display: flex;
                    .typography__text-regular();
                    padding: 20px 0;
                    overflow: hidden;
                    opacity: 0.6;
                    justify-content: space-between;
                    -webkit-animation: tracking-in-contract 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
                    animation: tracking-in-contract 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;

                    .description {
                        display: flex;
                        justify-content: space-between;
                        width: 50%;

                        @media @mobile {
                            max-width: 50px;
                            justify-content: flex-end !important;
                        }

                        &_year {
                            margin-left: 20px;
                        }

                        &_subtitle {
                            @media @mobile {
                                display: none !important;
                            }
                        }
                    }

                    &.active {
                        opacity: 1 !important;
                    }

                    &:not(.no-hover):after {
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        height: 1px;
                        width: 0%;
                        background-color: @color_primary_description;
                        animation: open-line 0.3s;
                        animation-fill-mode: forwards;
                    }

                    &:not(.no-hover):hover::after {
                        background-color: transparent;
                    }

                    &:not(.no-hover):hover::before {
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        height: 1px;
                        width: 0%;
                        animation-delay: 0s;
                        background-color: @color_primary_description;
                        animation: open-hover-line 0.5s;
                        animation-fill-mode: both;
                    }
                }
            }

            ul.mobile-list {
                display: none;
                margin-top: 100px;

                @media @mobile {
                    margin-top: 80px;
                    display: block;
                }

                li {
                    svg {
                        transition: all 0.3s linear;
                        opacity: 0;
                    }

                    a {
                        display: flex;
                        .typography__text-bold();
                        text-decoration: none;
                        justify-content: space-between;
                        width: 100%;
                        color: @color_primary_text;
                        align-items: center;
                    }

                    &:not(.no-hover):hover {
                        svg {
                            opacity: 1;
                        }
                    }
                }
            }

            ul.right-list {
                li {
                    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
                    animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
                    opacity: 1;

                    a {
                        display: flex;
                        .typography__text-bold();
                        text-decoration: none;
                        justify-content: space-between;
                        width: 100%;
                        color: @color_primary_text;
                        align-items: center;
                    }

                    svg {
                        transition: all 0.3s linear;
                        opacity: 0;
                    }

                    &:not(.no-hover):hover {
                        svg {
                            opacity: 1;
                        }
                    }

                    &:not(.no-hover):hover::after {
                        background-color: transparent;
                    }

                    &:not(.no-hover):hover::before {
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        height: 1px;
                        width: 0%;
                        animation-delay: 0s;
                        background-color: @color_primary_description;
                        animation: open-hover-line 0.5s;
                        animation-fill-mode: both;
                    }
                }
            }
        }
    }

    button {
        &.sound {
            background: transparent;
            border: none;
            display: flex;
            outline: none;
            align-items: center;
            justify-content: space-between;
            color: @color_primary_description;
            .typography__text-regular();
            cursor: pointer;
            margin-left: 100px;
            gap: 10px;

            @media @laptop {
                margin-left: 20px;
            }

            @media @mobile {
                margin-left: 0px;
                margin-top: 10px;
                padding-left: 0;
                flex-direction: row-reverse;
            }

            &.pause {
                .line-1,
                .line-2,
                .line-3 {
                    background-color: @color_primary_description;
                }
            }
        }
    }
}

