@keyframes go-up-down {
  0% {
    height: 0;
  }

  100% {
    height: 20px;
  }
}

@keyframes open-mask {
  0% {
    clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
  }

  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

@keyframes open-line {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes open-hover-line {
  0% {
    width: 0%;
  }

  100% {
    background-color: #ee0d0d;
    width: 100%;
  }
}

